@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap");
.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("../sunday.png") no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  width: vw;
}

.wrapper {
  padding: 25px;
  border: 0.1px solid rgb(96, 96, 96);
  display: flex;
  flex-direction: column;
  width: 30%;
  height: auto;
  background: rgba(219, 216, 216, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.app__image {
  margin: 24px;
  max-height: 500px;
  width: auto;
  border-radius: 10px;
}
.location_searcher {
  padding: 8px;
  color: white;
  background-color: #333;
  border: none;
  border-radius: 5px;
  margin-left: 5px;
}
.location_input {
  padding: 8px;
  border: 1px solid rgba(255, 255, 255, 0.6);
  color: #333;
  border-radius: 5px;
}

.temp {
  margin: 5px;
  font-family: "Bitter", serif;
  color: #2e2e2e;
}

.city {
  margin: 5px;
  font-family: "Bitter", serif;
  color: #2e2e2e;
}
.tempHiLo {
  margin: 5px;
  font-family: "Bitter", serif;
  color: #2e2e2e;
}
.currentConditions {
  margin: 5px;
  font-family: "Bitter", serif;
  color: #2e2e2e;
  text-transform: capitalize;
}
.humidity {
  margin: 5px;
  font-family: "Bitter", serif;
  color: #2e2e2e;
}
.feelsLike {
  margin: 5px;
  font-family: "Bitter", serif;
  color: #2e2e2e;
}
.currentDate {
  margin: 5px;
  font-family: "Bitter", serif;
  color: #2e2e2e;
}
